body{
  font-family: europa;
  letter-spacing: .04em;
}
.header{
  padding: 1.5em;
  display: flex;
  max-width: 60%;
  margin-left: auto;
  margin-right: auto;
}
.site-title{
  text-transform: uppercase;
  letter-spacing: .1em;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  display: flex;
  flex: 1;
}
.site-title a:link, .site-title a:visited{
  color:black;
  text-decoration: none;
}
.tab {
  margin-left: 20px;
  cursor: pointer;
  position: relative;
  transition-duration: .25s;
}
.tab:hover{
  color:black !important;
}
.tab:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #ea5e76;
  opacity: 0;
  transform: translateY(-8px);
}
.tab:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #ea5e76;
  opacity: 0;
  transform: translateY(3px);
  transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s;
}
.tab:hover:before, .tab:focus:before, .tab:hover:after, .tab:focus:after {
  opacity: 1;
  transform: translateY(0);
}
.tab:hover:before, .tab:focus:before {
  transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s;
}
.tab:hover:after, .tab:focus:after {
  transition: transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0s 0.2s;
}

