.left-nav{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex: 1;
  font-size:20px;
}
.right-nav{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex: 1;
  font-size: 20px;
}

a:link, a:visited{
  color: rgb(234, 94, 118);
  text-decoration: underline;
  transition-duration: 140ms;
  text-decoration-color: transparent;
}

a:hover{
  color:black;
  text-decoration: underline;
  text-decoration-color: black;
}

.main-image{
  width: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.main-text{
  text-align: center;
}
.content-title{
  font-size: 32px;
  font-weight: bold;
  line-height: 1.66em;
  
}
.content-body{
  display: grid;
  max-width: 60%;
  grid-template-columns: repeat(2, 1fr);
  margin-right: auto;
  margin-left: auto;
  column-gap: 3vw;
  row-gap: 3vw;
  align-items: center;
  justify-items: center;
}
.content-body-text{
  font-size: 20px;
  max-width: 100%;
  line-height: 1.66em;
}
@media only screen and (max-width: 990px) {
  .content-body-text{
      grid-column: span 2;
  }
  .content-body-image{
      grid-column: span 2;
  }
}
.content-body-image img{    
  display: block;
  width: 100%;
  min-width:100px;
  max-width: 100%;
  height: auto;
}
.gallery-container{
  display: grid;
  max-width: 80%;
  grid-template-columns: repeat(auto-fit, minmax(360px, auto));
  column-gap: 2vw;
  row-gap: 2vw;
  margin-right: auto;
  margin-left: auto;
  align-items: center;
  justify-items: center;
}
.photo{
  transition-duration:.4s;
}
.photo:hover{
  -moz-transform: scale(1.05);
  -webkit-transform: scale(1.05);
  -ms-transform: scale(1.05);
  transform: scale(1.05);
  cursor: pointer;
}
#box{
  max-width:100%;
  object-fit: cover;
  height:333px;
  border-radius: 4px;
}
#boxPortrait{
  object-fit:cover;
  width:100%;
  height:333px;
  border-radius: 4px;
  object-position: 50% 20%;
}

#lightbox{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:rgba(0, 0, 0, .8);
  display:none;
}

#lightbox img{
  position: fixed;
  max-width: 50%;
  max-height: 75%;
  z-index: 100;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#topBtn{
  display: none;
  position: fixed;
  bottom: 0;
  right: -2.5vw;
  z-index: 1;
  cursor: pointer;
}
#topBtn img{
  width: 45%;
}